.experience-container {
    padding: 2rem;
    background-size: cover; 
    background-position: center; 
    background-repeat: no-repeat; 
    min-width: 168vh;
    color: white;
    font-size: 1rem;
}
  

.experience-container h2 {
  text-align: center;
  font-size: 4rem;
  margin-bottom: 2rem;
}
  
.vertical-timeline-element-title {
    font-size: 1.5rem;
    font-weight: bold;
    text-align: left;
    margin-bottom: 2rem; 
}
  
.vertical-timeline-element-subtitle-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem; 
}
 
.vertical-timeline-element-subtitle {
    font-size: 1rem;
    color: #bdbfc7;
    text-align: left;
    font-weight: 500;
}

.vertical-timeline-element-location {
    font-size: 1rem;
    color: #bdbfc7;
    text-align: right;
    margin-left: auto;
    font-weight: 500;
}
  
.vertical-timeline-element-content {
  background-color: #23283d !important;
  box-shadow: 0 3px 10px rgba(255, 255, 255, 0.852);
  border-radius: 8px;
  color: #e6e8f1; 
  padding: 1rem;
  text-align: justify;
  transition: transform 0.5s ease, box-shadow 0.5s ease; 
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.5); 
  animation-duration: 1.2s; 
  animation-timing-function: ease-in-out; 
}

.left-card .vertical-timeline-element-content {
  animation: slideInFromLeft 1.5s ease-in-out both;
}

.right-card .vertical-timeline-element-content {
  animation: slideInFromRight 1.5s ease-in-out both; 
}


@keyframes slideInFromLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}


@keyframes slideInFromRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}


.vertical-timeline-element-content:hover {
  transform: translateY(-10px); 
  box-shadow: 0px 8px 15px rgba(255, 255, 255, 0.3); 
}

.vertical-timeline-element-content p {
    margin-bottom: 2rem 0;
}

.vertical-timeline-element-content ul {
    margin-bottom: 2;
    padding-left: 6px;
}

.vertical-timeline-element-content li {
    margin-bottom: 0.9rem;
}

.company-logo {
    width: 60px; 
    height: auto;
    border-radius: 50%;
  }
  
 .company-logos {
    width: 120px; 
    height: auto;
    border-radius: 50%;
  }

.company-logotd {
    width: 100%; 
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
  }


.tools-icons {
    margin-top: 1.8rem;
    display: flex;
    gap: 1.1rem;
  }
  
.tool-icon {
    width: 58px;
    height: 52px;
    cursor: pointer;
  }

 .tool-icon:hover {
    transform: scale(1.1);
    transition: transform 0.1s;
}
